var loadCssJS = (function (dom) {
    var cache = {}, /*cssjs*/
        funct;
    /**
     * HTMLcssjs
     * @path {String} src  link .js.css
     * @callback {Function} 
     * */
    funct = function (path, callback) {
        if (!path) { /**/
            throw new Error('path!');
        }
        const fn = Object.prototype.toString.call(callback) === '[object Function]' ? callback : function () {
        };
        if ('.js' === path.substr(-3)) { /*.js*/
            addJs(path, fn);
        } else if ('.css' === path.substr(-4)) { /*.css*/
            addCss(path, fn);
        } else {
            throw new Error('path!');
        }
    };

    /**
     * HTML  CSS
     * @path {String}  link .css
     * @callback {Function} 
     * */
    function addCss(path, callback) {
        if (!checkcache(path)) { /**/
            var head = dom.getElementsByTagName('head')[0];
            var link = dom.createElement('link');
            link.href = path;
            link.rel = 'stylesheet';
            link.type = 'text/css';
            head.appendChild(link); /*HTML*/
            link.onload = link.onreadystatechange = function () { /**/
                if (!this.readyState || this.readyState === 'loaded' || this.readyState === 'complete') {
                    link.onload = link.onreadystatechange = null;
                    callback();
                }
            };
            cache[path] = 1; /*css,1*/
        }
    }

    /**
     * HTMLjs
     * @path {String} src .js
     * @callback {Function} 
     * */
    function addJs(path, callback) {
        if (!checkcache(path)) { /**/
            const head = dom.getElementsByTagName('head')[0];
            const script = dom.createElement('script');
            script.src = path;
            script.type = 'text/javascript';
            head.appendChild(script); /*HTML*/
            script.onload = script.onreadystatechange = function () { /**/
                if (!this.readyState || this.readyState === 'loaded' || this.readyState === 'complete') {
                    script.onload = script.onreadystatechange = null;
                    callback();
                }
            };
            cache[path] = 1; /*js,1*/
        }
    }

    /**
     * 
     * @path {String} 
     * */
    function checkcache(path) {
        return !!cache[path];
    }
    return funct;
}(document));

export default loadCssJS;



